import React, { useState, useEffect } from 'react';
import axios from "axios"
import {useParams} from "react-router-dom";
import Sidebar from '../Components/Sidebar';
import Comments from '../Components/Comments';

const Post = () => {
    let { id } = useParams();

    const postUrl = `https://jsonplaceholder.typicode.com/posts/${id}`;
    const [post, setPost] = useState(null);
    const [author, setAuthor] = useState(null);
   
    const getPost = async() => {
        try{
            const resp = await axios.get(postUrl);
            setPost(resp.data);
        }
        catch(error){
            console.error(error);
        }
    }

    const getAuthor = async() => {
        if(post) 
        try{
            let authorURL = `https://jsonplaceholder.typicode.com/users/${post?.userId}`
            const resp = await axios.get(authorURL);
            setAuthor(resp.data);
        }
        catch(error){
            console.error(error);
        }
    }

    useEffect(() => {
        const fetchData = async () => {
          await getPost()
          await getAuthor()
        };

        fetchData();
      }, [post]);

      return (
        <>
            {/* Post Title */}
            {post && author && 
            <div>
             <h1 style={{display:'inline'}}> {post.title}</h1> 
             {/* Right Side bar with Author details */}
             <Sidebar placement={'end'} buttonName={'Know the Author'} authorName={author.name} 
                 authorEmail={author.email} authorCity={author.address.city} authorWebsite={author.website}
             />
            
            
          
            <br/>
            

            <p>{post.body} {post.body} {post.body} {post.body} {post.body} {post.body} {post.body} {post.body} 
            {post.body} {post.body} {post.body} {post.body} {post.body} {post.body} {post.body} {post.body} 
            {post.body} {post.body} {post.body} {post.body}</p>

            <Comments userId={id}/>
            
            </div>}
        </>
    )
}

export default Post
