import React, {useEffect, useState} from 'react'
import axios from "axios"
import {Row, Col, Card, Button} from "react-bootstrap"
import {Link} from "react-router-dom";

function Posts() {
    const url = "https://jsonplaceholder.typicode.com/posts/";
    let [posts, setPosts] = useState(null);

    const getPosts = async() => {
        try{
            const resp = await axios.get(url);
            setPosts(resp.data);
            console.table(posts);
        }
        catch(error){
            console.error(error);
        }
    }

    useEffect(() => {
        console.log('use effect getting posts');
        getPosts();
        return () => {
            console.log('Use effect clean');
        }
    }, [])

     return (
        <>
        {posts &&
        <Row xs={1} md={4} className="g-4">
  {posts.map((post, idx) => (
    <Col key={idx}>
      <Card >
        <Card.Img variant="top" src={`https://source.unsplash.com/random/150x150?sig=${idx}`} />
        <Card.Body>
          <Card.Title>{post.title}</Card.Title>
          <Card.Text>
            {post.body}
          </Card.Text>
          <Link to={`/post/${post.id}`}> <Button variant="primary">Read Now</Button> </Link>
        </Card.Body>
      </Card>
    </Col>
  ))}
</Row>}
        </>
    )
}

export default Posts;
