import React, {useState} from 'react'
import {Offcanvas, Button} from "react-bootstrap";

function Sidebar({ buttonName, authorName, authorEmail, authorCity, authorWebsite, ...props }) {
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <div>
            <Button variant="primary" onClick={handleShow} className="me-2">
          {buttonName}
        </Button>
        <Offcanvas show={show} onHide={handleClose} {...props}>
          <Offcanvas.Header closeButton>
            <Offcanvas.Title>About The Author</Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body>
            <h4>Name:- {authorName}</h4>
            <h5>Email:- {authorEmail}</h5>
            <h5>City:- {authorCity}</h5>
            <h5>Website:- {authorWebsite}</h5>
          </Offcanvas.Body>
        </Offcanvas>
        </div>
    );
}

export default Sidebar
